import s from './Dialogs.module.css';
import DialogItem from './DialogItem/DialogItem';
import Message from './Message/Message';
import React from 'react';

export default function Dialogs(props) {
  const dialogElements = props.dialogsData.map(data => <DialogItem id={data.id} key={data.id} name={data.name} />);
  const messageElements = props.messagesData.map(data => <Message message={data.message} key={data.id} sent={data.sent} />);

  return (
    <div className={s.dialogs}>
      <div className={s.dialogItems}>
        {dialogElements}
      </div>
      <div className={s.messages}>
        {messageElements}
        <div className={s.newMessage}>
          <textarea onChange={props.updateNewMessageText} value={props.newMessageText} placeholder='Type your message here...'></textarea>
          <button onClick={props.addMessage}>Send</button>
        </div>
      </div>
    </div>
  )
}